.tooltip {
  /* display:inline-block; */
  position: relative;
  /* border-bottom:1px dotted #666; */
  /* text-align:left; */
}
.tooltip .right {
  min-width: 0px;
  top: 50%;
  left: 100%;
  margin-left: 10px;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  padding: 5px 10px;
  color: #ffffff;
  background-color: black;
  /* font-weight: normal; */
  font-size: 15px;
  border-radius: 4px;
  position: absolute;
  z-index: 99999999;
  /* box-sizing: border-box; */
  border: 0.5px solid gray;
  /* box-shadow: 0 1px 8px rgba(0, 0, 0, 0.5); */
  visibility: hidden;
  /* opacity: 0; */
  -webkit-transition: opacity 1s;
  transition: opacity 1s;
}

.tooltip:hover .right {
  visibility: visible;
  opacity: 1;
}

.tooltip .right i {
  position: absolute;
  top: 50%;
  right: 100%;
  margin-top: -0px;
  width: 0px;
  height: 0px;
  overflow: hidden;
}

.tooltip .right i::after {
  content: "";
  position: absolute;
  width: 0px;
  height: 0px;
  left: 0;
  top: 50%;
  -webkit-transform: translate(50%, -50%) rotate(-45deg);
          transform: translate(50%, -50%) rotate(-45deg);
  background-color: #222222;
  border: 1px solid #222222;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.5);
}

